/* Input Thumb */
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 32px;
  width: 32px;
  border-radius: 50%;
  background: #F9D549;
  cursor: ew-resize;
  transition: background .3s ease-in-out; }

input[type="range"]::-moz-range-thumb {
  -webkit-appearance: none;
  height: 32px;
